.container {
  width: 100%;
  height: 250px;
  background-color: #545157;

  .iconPanel {
    padding: 30px 40px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .linkPanel {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0 70px;
    .linkColum{
      display: flex;
      flex-direction: column;
      gap: 5px;
    }
    a,div{
      text-decoration: none;
      color: var(--textSecondColor);
    }
  }
}